/**
 * WordPress dependencies
 */
import {
    __experimentalVStack as VStack,
} from "@wordpress/components";
import {__} from "@wordpress/i18n";

/**
 * Plover dependencies
 */
import {UnitSlider} from "@plover/components";

export default function GenericShadowEditor({offsets, setOffsets}) {
    const [x, y, blur] = offsets;
    const updateOffsets = (obj) => {
        const tmp = {
            x, y, blur,
            ...obj
        };

        setOffsets([tmp.x, tmp.y, tmp.blur])
    }

    return (
        <VStack spacing="12px">
            <UnitSlider
                label={__('Horizontal Offset', 'plover')}
                value={x ?? '0px'}
                onChange={(value) => {
                    updateOffsets({x: value});
                }}
            />
            <UnitSlider
                label={__('Vertical Offset', 'plover')}
                value={y ?? '0px'}
                onChange={(value) => {
                    updateOffsets({y: value});
                }}
            />
            <UnitSlider
                label={__('Blur', 'plover')}
                value={blur ?? '0px'}
                min={0}
                onChange={(value) => {
                    updateOffsets({blur: value});
                }}
            />
        </VStack>
    );
}