/**
 * WordPress dependencies
 */
import {addFilter} from "@wordpress/hooks";
import {createHigherOrderComponent} from "@wordpress/compose";

/**
 * Plover dependencies
 */
import {getPloverSupport, hasPloverSupport} from "@plover/utils";

/**
 * Internal dependencies
 */
import ShadowPanel from "./ShadowPanel";
import {getBoxShadowValue, getDropShadowValue, getTextShadowValue} from './utils';

/**
 * Stylesheet
 */
import "./style.scss";

const isSupportShadow = (name) => {
    return hasPloverSupport(name, 'ploverShadow');
}

const isSupportTextShadow = (name) => {
    return getPloverSupport(name, 'ploverShadow')?.text ?? false;
}

const isSupportDropShadow = (name) => {
    return getPloverSupport(name, 'ploverShadow')?.drop ?? false;
}

const isSupportBoxShadow = (name) => {
    return getPloverSupport(name, 'ploverShadow')?.box ?? false;
}

addFilter(
    'blocks.registerBlockType',
    'plover/shadow-attributes',
    (settings, name) => {

        if (isSupportTextShadow(name)) {
            settings.attributes = {
                ...settings.attributes,
                textShadow: {
                    type: 'string'
                }
            };
        }

        if (isSupportDropShadow(name)) {
            settings.attributes = {
                ...settings.attributes,
                dropShadow: {
                    type: 'string'
                }
            };
        }

        if (isSupportBoxShadow(name)) {
            settings.attributes = {
                ...settings.attributes,
                boxShadow: {
                    type: 'string'
                }
            };
        }

        return settings;
    }
);

addFilter(
    'editor.BlockListBlock',
    'plover/with-shadow-style',
    createHigherOrderComponent((BlockListBlock) => {
        return (props) => {
            const {attributes, name} = props;

            if (!isSupportShadow(name)) {
                return <BlockListBlock {...props}/>
            }

            const styles = {
                textShadow: isSupportTextShadow(name) && getTextShadowValue(attributes?.textShadow),
                boxShadow: isSupportBoxShadow(name) && getBoxShadowValue(attributes?.boxShadow),
                filter:
                    ((props?.wrapperProps?.style?.filter ?? '')
                        + ' ' + (props?.style?.filter ?? '')
                        + ' ' + (isSupportDropShadow(name) && getDropShadowValue(attributes?.dropShadow)))
                        .trim()
                ,
            };

            props = {
                ...props,
                style: {...props?.style, ...styles},
            };

            let wrapperProps = props?.wrapperProps ?? {};
            if (wrapperProps) {
                wrapperProps.style = {...wrapperProps?.style, ...styles};
            }

            props.wrapperProps = wrapperProps;

            return <BlockListBlock {...props}/>;
        };
    }, 'withShadowStyle')
);

addFilter(
    'editor.BlockEdit',
    'plover/shadow-controls',
    (BlockEdit) => {
        return (props) => {
            const {isSelected, name} = props;
            if (!isSupportShadow(name)) {
                return <BlockEdit {...props}/>
            }

            return (
                <>
                    <BlockEdit {...props}/>
                    {isSelected && (
                        <ShadowPanel
                            {...props} {...getPloverSupport(props.name, 'ploverShadow')}
                        />
                    )}
                </>
            );
        }
    }
);

