/**
 * WordPress dependencies
 */
import {
    Button,
    __experimentalDropdownContentWrapper as DropdownContentWrapper,
    __experimentalVStack as VStack,
} from "@wordpress/components";
import {__, sprintf} from "@wordpress/i18n";
import {symbol as dropShadowIcon, check, Icon} from '@wordpress/icons';

/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * Plover dependencies
 */
import {getPloverThemeSettings} from "@plover/utils";
import {polygon} from "@plover/icons";
import {Popover} from "@plover/components";

/**
 * Internal dependencies
 */
import ShadowValues from "./ShadowValues";
import GenericShadowEditor from "./GenericShadowEditor";

import {
    DROP_SHADOW_VAR_PREFIX,
    dropShadowToGenericShadow,
    genericShadowsToDropShadowString,
    getDropShadowLabel,
    getDropShadowValue
} from "./utils";

function DropShadowPreview({className, name, shadow, checked, textEllipsis}) {

    return (
        <div
            className={clsx('plover-shadow-settings-panel__drop-shadow-preview', className)}
        >
            {checked && (
                <Icon
                    icon={check}
                    size={28}
                    className={"plover-shadow-settings-panel__shadow-checked"}
                />
            )}

            <Icon className="polygon-shadow-preview" icon={polygon} size={52} style={{
                filter: getDropShadowValue(shadow)
            }}/>
            <span className={clsx({'text-ellipsis': textEllipsis})}>{name}</span>
        </div>
    );
}

function renderDropShadowEditor({attributes, setAttributes}) {
    const {dropShadow} = attributes;
    const presets = getPloverThemeSettings('dropShadow.presets') ?? [];

    const setDropShadow = (value) => {
        setAttributes({
            dropShadow: value
        })
    }

    return (
        <DropdownContentWrapper
            paddingSize="none"
            className="plover-shadow-settings-panel__popover-content plover-shadow-settings-panel__drop-shadow-editor"
        >
            <VStack spacing="12px" className="plover-shadow-settings-panel__drop-shadow-editor">
                <div className="plover-shadow-settings-panel__drop-shadow-preview-canvas">
                    <DropShadowPreview
                        name={sprintf(
                            /* translators: %s is the name of shadow, e.g., "custom/solid" */
                            __('Preview / %s', 'plover'),
                            getDropShadowLabel(dropShadow) ?? ''
                        )}
                        textEllipsis={false}
                        shadow={dropShadow}
                    />
                </div>

                <ShadowValues
                    shadows={dropShadowToGenericShadow(getDropShadowValue(dropShadow))}
                    onChange={(shadows) => {
                        setDropShadow(genericShadowsToDropShadowString(shadows));
                    }}
                    renderEditor={GenericShadowEditor}
                />
            </VStack>

            <div className="plover-shadow-settings-panel__drop-shadow-presets">
                <h3>{__('Presets', 'plover')}</h3>
                <div className="plover-shadow-settings-panel__drop-shadow-preview-canvas">
                    <Button
                        className={clsx({
                            'active': !dropShadow || dropShadow === 'none' || dropShadow === 'unset'
                        })}
                        onClick={() => {
                            setDropShadow(null);
                        }}
                    >
                        <DropShadowPreview
                            name={__('Unset', 'plover')}
                            checked={!dropShadow || dropShadow === 'none' || dropShadow === 'unset'}
                            shadow={null}
                        />
                    </Button>

                    {presets.map((preset) => {
                        const presetShadowValue = `${DROP_SHADOW_VAR_PREFIX}${preset.slug}`;

                        return (
                            <Button
                                key={preset.slug}
                                className={clsx({
                                    'active': dropShadow === presetShadowValue
                                })}
                                onClick={() => {
                                    setDropShadow(presetShadowValue);
                                }}
                            >
                                <DropShadowPreview
                                    {...preset}
                                    checked={dropShadow === presetShadowValue}
                                    shadow={presetShadowValue}
                                />
                            </Button>
                        )
                    })}
                </div>
            </div>
            <Button variant="tertiary" onClick={() => setDropShadow(null)}>
                {__('Clear', 'plover')}
            </Button>
        </DropdownContentWrapper>
    );
}

export default function DropShadowControls(props) {

    return (
        <Popover
            toggle={{
                icon: dropShadowIcon,
                label: __('Drop shadow', 'plover')
            }}
            renderContent={() => renderDropShadowEditor(props)}
        />
    );
}
