/**
 * WordPress dependencies
 */
import { InspectorControls } from '@wordpress/block-editor';
import { useState } from '@wordpress/element';
import {
  __experimentalToolsPanelItem as ToolsPanelItem,
  __experimentalItemGroup as ItemGroup,
  __experimentalToolsPanel as ToolsPanel,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import TextShadowControls from './TextShadowControls';
import DropShadowControls from './DropShadowControls';
import BoxShadowControls from './BoxShadowControls';
import { Tips } from '@plover/components';

const ShadowPanel = (props) => {
  const { text, drop, box, defaultControls, attributes } = props;

  if (!text && !drop && !box) {
    return null;
  }

  const [textShadow, setTextShadow] = useState();
  const [dropShadow, setDropShadow] = useState();
  const [boxShadow, setBoxShadow] = useState();

  const resetAll = () => {
    setTextShadow(undefined);
    setDropShadow(undefined);
    setBoxShadow(undefined);
  };

  return (
    <InspectorControls group="styles">
      <ToolsPanel resetAll={resetAll} label={__('Shadow', 'plover')}>
        {text && (
          <ToolsPanelItem
            hasValue={() => !!textShadow}
            label={__('Text Shadow', 'plover')}
            onDeselect={() => setTextShadow(undefined)}
            isShownByDefault={defaultControls?.text || attributes?.textShadow}
          >
            <ItemGroup isBordered isSeparated>
              <TextShadowControls
                {...props}
              />
            </ItemGroup>
          </ToolsPanelItem>
        )}
        {drop && (
          <ToolsPanelItem
            hasValue={() => !!dropShadow}
            label={__('Drop Shadow', 'plover')}
            onDeselect={() => setDropShadow(undefined)}
            isShownByDefault={defaultControls?.drop || attributes?.dropShadow}
          >
            <ItemGroup isBordered isSeparated>
              <DropShadowControls
                {...props}
              />
            </ItemGroup>
          </ToolsPanelItem>
        )}
        {box && (
          <ToolsPanelItem
            hasValue={() => !!boxShadow}
            label={__('Box Shadow', 'plover')}
            onDeselect={() => setBoxShadow(undefined)}
            isShownByDefault={defaultControls?.box || attributes?.boxShadow}
          >
            <ItemGroup isBordered isSeparated>
              <BoxShadowControls
                {...props}
              />
            </ItemGroup>
          </ToolsPanelItem>
        )}

        <Tips style={{ gridColumn: 'span 2' }}>
          <a href="https://wpplover.com/docs/plover-kit/modules/block-shadow/" target="_blank">
            {__('Documentation ', 'plover')}
          </a>
        </Tips>
      </ToolsPanel>
    </InspectorControls>
  );
};

export default ShadowPanel;
