/**
 * WordPress dependencies
 */
import {
    Button,
    __experimentalDropdownContentWrapper as DropdownContentWrapper,
    __experimentalVStack as VStack,
} from "@wordpress/components";
import {__, sprintf} from "@wordpress/i18n";
import {overlayText as textShadowIcon} from '@wordpress/icons';

/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * Plover dependencies
 */
import {getPloverThemeSettings} from "@plover/utils";
import {Popover} from "@plover/components";

/**
 * Internal dependencies
 */
import ShadowValues from "./ShadowValues";
import GenericShadowEditor from "./GenericShadowEditor";
import {TEXT_SHADOW_VAR_PREFIX, getTextShadowValue, getTextShadowLabel} from './utils';

function TextShadowPreview({className, name, shadow, ...props}) {

    return (
        <div className={clsx("plover-shadow-settings-panel__text-shadow-preview", className)} {...props}>
            <span>{name}</span>
            <p style={{textShadow: getTextShadowValue(shadow)}}>
                Lorem ipsum preview.
            </p>
        </div>
    );
}

function renderTextShadowEditor({attributes, setAttributes}) {
    const {textShadow} = attributes;
    const presets = getPloverThemeSettings('textShadow.presets') ?? [];

    const setTextShadow = (value) => {
        setAttributes({
            textShadow: value
        });
    }

    return (
        <DropdownContentWrapper
            paddingSize="none"
            className="plover-shadow-settings-panel__popover-content plover-shadow-settings-panel__text-shadow-editor"
        >
            <VStack spacing="12px">
                <TextShadowPreview
                    name={sprintf(
                        /* translators: %s is the name of shadow, e.g., "custom/solid" */
                        __('Preview / %s', 'plover'),
                        getTextShadowLabel(textShadow) ?? ''
                    )}
                    shadow={textShadow}
                />

                <ShadowValues
                    shadows={getTextShadowValue(textShadow)}
                    onChange={(v) => setTextShadow(v.join(','))}
                    renderEditor={GenericShadowEditor}
                />
            </VStack>
            <div className="plover-shadow-settings-panel__text-shadow-presets">
                <h3>{__('Presets', 'plover')}</h3>

                <Button
                    className={clsx({
                        'active': !textShadow || textShadow === 'none' || textShadow === 'unset'
                    })}
                    onClick={() => {
                        setTextShadow(null);
                    }}
                >
                    <TextShadowPreview
                        name={__('Unset', 'plover')}
                        shadow={null}
                    />
                </Button>

                {presets.map((preset) => {
                    const presetShadowValue = `${TEXT_SHADOW_VAR_PREFIX}${preset.slug}`;

                    return (
                        <Button
                            key={preset.slug}
                            className={clsx({
                                'active': textShadow === presetShadowValue
                            })}
                            onClick={() => {
                                setTextShadow(presetShadowValue);
                            }}
                        >
                            <TextShadowPreview
                                {...preset}
                                shadow={presetShadowValue}
                            />
                        </Button>
                    )
                })}
            </div>

            <Button variant="tertiary" onClick={() => setTextShadow(null)}>
                {__('Clear', 'plover')}
            </Button>
        </DropdownContentWrapper>
    );
}

export default function TextShadowControls(props) {
    return (
        <Popover
            toggle={{
                icon: textShadowIcon,
                label: __('Text shadow', 'plover')
            }}
            renderContent={() => renderTextShadowEditor(props)}
        />
    );
}
