/**
 * WordPress dependencies
 */
import {
  Button,
  ToggleControl,
  __experimentalDropdownContentWrapper as DropdownContentWrapper,
  __experimentalVStack as VStack,
} from '@wordpress/components';
import { __, sprintf } from '@wordpress/i18n';
import { shadow as boxShadowIcon, check, Icon } from '@wordpress/icons';

/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * Plover dependencies
 */
import { Popover, UnitSlider } from '@plover/components';
import { getPloverThemeSettings, useSettings } from '@plover/utils';

/**
 * Internal dependencies
 */
import ShadowValues from './ShadowValues';
import { BOX_SHADOW_CUSTOM_VAR_PREFIX, BOX_SHADOW_VAR_PREFIX, getBoxShadowLabel, getBoxShadowValue } from './utils';

function BoxShadowPreview ({ className, name, shadow, checked, textEllipsis }) {
  return (
    <div
      className={clsx('plover-shadow-settings-panel__box-shadow-preview', className)}
    >
      {checked && (
        <Icon
          icon={check}
          size={28}
          className={'plover-shadow-settings-panel__shadow-checked'}
        />
      )}

      <div className="square-shadow-preview" style={{
        boxShadow: getBoxShadowValue(shadow),
      }}/>

      <span className={clsx({ 'text-ellipsis': textEllipsis })}>{name}</span>
    </div>
  );
}

function renderBoxShadowEditor ({ presets, attributes, setAttributes }) {
  const { boxShadow } = attributes;

  const setBoxShadow = (value) => {
    setAttributes({
      boxShadow: value
    });
  };

  return (
    <DropdownContentWrapper
      paddingSize="none"
      className="plover-shadow-settings-panel__popover-content plover-shadow-settings-panel__box-shadow-editor"
    >
      <VStack spacing="12px" className="plover-shadow-settings-panel__drop-shadow-editor">
        <div className="plover-shadow-settings-panel__drop-shadow-preview-canvas">
          <BoxShadowPreview
            name={sprintf(
              /* translators: %s is the name of shadow, e.g., "custom/solid" */
              __('Preview / %s', 'plover'),
              getBoxShadowLabel(boxShadow, presets) ?? ''
            )}
            textEllipsis={false}
            shadow={boxShadow}
          />
        </div>

        <ShadowValues
          shadows={getBoxShadowValue(boxShadow)}
          onChange={(v) => setBoxShadow(v.join(','))}
          renderEditor={({ offsets, setOffsets }) => {
            const hasInset = offsets.indexOf('inset') !== -1;
            const [x, y, blur, spread] = offsets.filter((i) => i !== 'inset');
            const updateOffsets = (obj) => {
              const v = {
                x, y, blur, spread,
                inset: hasInset,
                ...obj,
              };

              setOffsets([
                v.inset ? 'inset' : null,
                v.x, v.y, v.blur, v.spread
              ].filter((i) => i !== null));
            };

            return (
              <VStack spacing="12px">
                <ToggleControl
                  label={__('Inset', 'plover')}
                  checked={hasInset}
                  onChange={(value) => {
                    updateOffsets({ inset: value });
                  }}
                />

                <UnitSlider
                  label={__('Horizontal Offset', 'plover')}
                  value={x ?? '0px'}
                  onChange={(value) => {
                    updateOffsets({ x: value });
                  }}
                />
                <UnitSlider
                  label={__('Vertical Offset', 'plover')}
                  value={y ?? '0px'}
                  onChange={(value) => {
                    updateOffsets({ y: value });
                  }}
                />
                <UnitSlider
                  label={__('Blur', 'plover')}
                  value={blur ?? '0px'}
                  min={0}
                  onChange={(value) => {
                    updateOffsets({ blur: value });
                  }}
                />
                <UnitSlider
                  label={__('Spread', 'plover')}
                  value={spread ?? '0px'}
                  min={0}
                  onChange={(value) => {
                    updateOffsets({ spread: value });
                  }}
                />
              </VStack>
            );
          }}
        />
      </VStack>

      <div className="plover-shadow-settings-panel__box-shadow-presets">
        <h3>{__('Presets', 'plover')}</h3>
        <div className="plover-shadow-settings-panel__box-shadow-preview-canvas">
          <Button
            className={clsx({
              'active': !boxShadow || boxShadow === 'none' || boxShadow === 'unset'
            })}
            onClick={() => {
              setBoxShadow(null);
            }}
          >
            <BoxShadowPreview
              name={__('Unset', 'plover')}
              checked={!boxShadow || boxShadow === 'none' || boxShadow === 'unset'}
              shadow={null}
            />
          </Button>

          {presets.map((preset) => {
            const presetShadowValue = `${preset.prefix}${preset.slug}`;

            return (
              <Button
                key={preset.slug}
                className={clsx({
                  'active': boxShadow === presetShadowValue
                })}
                onClick={() => {
                  setBoxShadow(presetShadowValue);
                }}
              >
                <BoxShadowPreview
                  {...preset}
                  checked={boxShadow === presetShadowValue}
                  shadow={presetShadowValue}
                />
              </Button>
            );
          })}
        </div>
      </div>
    </DropdownContentWrapper>
  );
}

export default function BoxShadowControls (props) {

  const [shadow] = useSettings('shadow');
  const presets = [
    ...[
      ...(shadow.defaultPresets ? (shadow?.presets?.default ?? []) : []),
      ...(shadow?.presets?.theme ?? [])
    ].map((p) => {
      p.prefix = BOX_SHADOW_VAR_PREFIX;
      return p;
    }),
    ...(getPloverThemeSettings('boxShadow.presets') ?? []).map((p) => {
      p.prefix = BOX_SHADOW_CUSTOM_VAR_PREFIX;
      return p;
    })
  ];

  return (
    <Popover
      toggle={{
        icon: boxShadowIcon,
        label: __('Box shadow', 'plover')
      }}
      renderContent={() => renderBoxShadowEditor({ presets, ...props })}
    />
  );
}
