/**
 * WordPress dependencies
 */
import {__} from "@wordpress/i18n";

/**
 * Plover dependencies
 */
import {
    getBodyComputedStyle,
    getPloverThemeSettings,
    isString,
    replaceColorsWithPlaceholder
} from "@plover/utils";

export const TEXT_SHADOW_VAR_PREFIX = 'var:custom|textShadow|';
export const DROP_SHADOW_VAR_PREFIX = 'var:custom|dropShadow|';
export const BOX_SHADOW_VAR_PREFIX = 'var:preset|shadow|';
export const BOX_SHADOW_CUSTOM_VAR_PREFIX = 'var:custom|boxShadow|';

function getPreviewLabel(presets, var_prefix, value) {
    let label = __('Unset', 'plover');
    if (isString(value)) {
        label = __('Custom', 'plover');
        if (value.startsWith(var_prefix)) {
            const preset = value.replace(var_prefix, '');
            label = presets.find((p) => p.slug === preset)?.name ?? label;
        }
    }

    return label;
}

export function getTextShadowLabel(shadow) {
    const presets = getPloverThemeSettings('textShadow.presets');
    return getPreviewLabel(presets, TEXT_SHADOW_VAR_PREFIX, shadow);
}

export function getDropShadowLabel(shadow) {
    const presets = getPloverThemeSettings('dropShadow.presets');
    return getPreviewLabel(presets, DROP_SHADOW_VAR_PREFIX, shadow);
}

export function getBoxShadowLabel(shadow, presets) {
    return getPreviewLabel(presets, BOX_SHADOW_VAR_PREFIX, shadow);
}

export function getTextShadowValue(shadow) {
    if (isString(shadow) && shadow.startsWith(TEXT_SHADOW_VAR_PREFIX)) {
        shadow = shadow.replace(TEXT_SHADOW_VAR_PREFIX, '');
        shadow = `var(--wp--custom--text-shadow--${shadow})`;
    }

    return shadow;
}

export function getBoxShadowValue(shadow) {
    if (isString(shadow) && shadow.startsWith(BOX_SHADOW_VAR_PREFIX)) {
        shadow = shadow.replace(BOX_SHADOW_VAR_PREFIX, '');
        shadow = `var(--wp--preset--shadow--${shadow})`;
    }
    if (isString(shadow) && shadow.startsWith(BOX_SHADOW_CUSTOM_VAR_PREFIX)) {
        shadow = shadow.replace(BOX_SHADOW_CUSTOM_VAR_PREFIX, '');
        shadow = `var(--wp--custom--box-shadow--${shadow})`;
    }

    return shadow;
}

export function getDropShadowValue(shadow) {
    if (isString(shadow) && shadow.startsWith(DROP_SHADOW_VAR_PREFIX)) {
        shadow = shadow.replace(DROP_SHADOW_VAR_PREFIX, '');
        shadow = `var(--wp--custom--drop-shadow--${shadow})`;
    }

    return shadow;
}

export function dropShadowToGenericShadow(shadow) {
    let rawValue = isString(shadow) ? getBodyComputedStyle(shadow) : '';

    return replaceColorsWithPlaceholder(rawValue, function (replacedValue, restore) {
        let dropShadowRegex = /drop-shadow\((.*?)\)/g;
        return (replacedValue.match(dropShadowRegex) ?? []).map((v) => {
            return restore(v
                .replace('drop-shadow(', '')
                .replace(')', '')
                .trim());
        }).join(',');
    });
}

export function genericShadowsToDropShadowString(shadows) {
    let v = '';

    shadows.forEach(s => {
        v += ` drop-shadow(${s})`
    });

    return v.trim();
}
