/**
 * WordPress dependencies
 */
import {
  Button,
  Dropdown,
  __experimentalDropdownContentWrapper as DropdownContentWrapper,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
  close,
  plusCircle,
  Icon
} from '@wordpress/icons';

/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * Plover dependencies
 */
import { ColorPopover } from '@plover/components';
import { shadow } from '@plover/icons';
import {
  extractColors,
  getBodyComputedStyle,
  isString,
  replaceColorsWithPlaceholder
} from '@plover/utils';

const popoverProps = {
  placement: 'left-start',
  offset: 36,
  shift: true,
};

function ShadowValueItem ({ value, renderEditor, onChange, onRemove }) {
  const color = extractColors(value)[0] ?? '';

  const offsets =
    (color ? value.replace(color, '') : value)
      .trim()
      .split(' ')
      .map(v => v.toLowerCase().trim());

  const setOffsets = (newOffsets) => {
    onChange(newOffsets.join(' ') + ' ' + color);
  };

  const setColor = (newColor) => {
    onChange(offsets.join(' ') + ' ' + newColor);
  };

  return (
    <div className={'plover-shadow-settings-panel__shadow-value-item'}>
      <div className={'plover-shadow-settings-panel__shadow-value'}>
        <Dropdown
          className={'plover-shadow-settings-panel__shadow-value-popover'}
          popoverProps={popoverProps}
          renderToggle={({ onToggle, isOpen }) => {
            const toggleProps = {
              onClick: onToggle,
              className: clsx(
                'plover-shadow-settings-panel__shadow-value-popover-toggle',
                { 'is-open': isOpen }
              ),
              'aria-expanded': isOpen,
              'aria-label': __('Shadow', 'plover'),
            };

            return (
              <Button {...toggleProps}>
                <Icon icon={shadow} size={20}/>
                <span>{offsets.join(' ')}</span>
              </Button>
            );
          }}
          renderContent={() => (
            <DropdownContentWrapper
              paddingSize="none"
              className="plover-shadow-settings-panel__shadow-value-popover-content"
            >
              {renderEditor({ offsets, setOffsets })}
            </DropdownContentWrapper>
          )}
        />

        <ColorPopover
          excludePresets={['transparent', 'current', 'inherit']}
          value={color}
          enableAlpha={true}
          className={'plover-shadow-settings-panel__shadow-color-popover'}
          onChange={setColor}
        />
      </div>
      <div className={'plover-shadow-settings-panel__shadow-value-controls'}>
        <Button onClick={onRemove}>
          <Icon icon={close} size={18}/>
        </Button>
      </div>
    </div>
  );
}

export default function ShadowValues (props) {
  const {
    shadows,
    onChange,
    renderEditor,
    defaultLayer = '0.1em 0.1em 0.1em rgba(0,0,0,0.5)'
  } = props;

  let rawValue = isString(shadows) ? getBodyComputedStyle(shadows) : '';
  const rawValues = replaceColorsWithPlaceholder(rawValue, function (replacedValue, restore) {
    return replacedValue.split(',').map((shadow) => {
      return restore(shadow);
    }).filter(i => i);
  });

  return (
    <>
      <div className={'plover-shadow-settings-panel__shadow-values'}>
        {rawValues.map((shadow, index) => {
          if (!shadow) {
            return null;
          }

          return (
            <ShadowValueItem
              value={shadow}
              key={index}
              onRemove={() => {
                onChange([...rawValues].filter((v, i) => {
                  return i !== index;
                }));
              }}
              onChange={(value) => {
                const newShadowValues = [...rawValues];
                newShadowValues[index] = value;
                onChange(newShadowValues);
              }}
              renderEditor={renderEditor}
            />
          );
        })}
      </div>

      <Button
        variant="primary" icon={plusCircle}
        className="plover-shadow-settings-panel__shadow-add-layer-button"
        onClick={() => {
          onChange([
            ...rawValues,
            defaultLayer
          ]);
        }}
      >
        {__('Add layer', 'plover')}
      </Button>
    </>
  );
}
